import React, { useEffect, useRef, useState } from 'react';
import * as THREE from 'three';

const CelebrationScreen = ({ onRestart }) => {
  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      backgroundColor: 'rgba(0, 255, 0, 0.8)',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      color: 'white',
      fontSize: '3rem',
      zIndex: 1000
    }}>
      <h1>YOU WIN!</h1>
    </div>
  );
};

const FPSControls = () => {
  const mountRef = useRef(null);
  const cameraRef = useRef(null);
  const sceneRef = useRef(null);
  const rendererRef = useRef(null);
  const crosshairRef = useRef(null);
  const minimapRef = useRef(null);
  const minimapCtxRef = useRef(null);
  const keysPressed = useRef({});
  const velocity = useRef(new THREE.Vector3());
  const direction = useRef(new THREE.Vector3());
  const prevTime = useRef(performance.now());
  const pointerLockEnabled = useRef(false);
  
  // Store walls for minimap rendering and collision
  const wallsRef = useRef([]);
  
  // Camera rotation state
  const cameraRotation = useRef({
    x: 0,  // Pitch (looking up/down)
    y: 0   // Yaw (looking left/right)
  });

  // Texture loading cache
  const textureCache = useRef(new Map());

  // State for victory
  const [hasWon, setHasWon] = useState(false);

  // Optimized texture loading function
  const loadTexture = (url) => {
    if (textureCache.current.has(url)) {
      return textureCache.current.get(url);
    }
    
    const textureLoader = new THREE.TextureLoader();
    const texture = textureLoader.load(url, 
      // OnLoad callback
      () => {
        // Optional: Set texture parameters here
        texture.wrapS = THREE.RepeatWrapping;
        texture.wrapT = THREE.RepeatWrapping;
        texture.needsUpdate = true;
      },
      // OnProgress (optional)
      undefined,
      // OnError
      (error) => {
        console.error('Error loading texture:', error);
      }
    );
    
    textureCache.current.set(url, texture);
    return texture;
  };

  // Collision detection function
  const checkWallCollision = (camera, walls, proposedPosition) => {
    const playerRadius = 0.5; // Approximate player collision radius
    
    for (const wall of walls) {
      // Create a bounding box for the wall
      const wallBox = new THREE.Box3().setFromObject(wall.mesh);
      
      // Expand the wall's bounding box to include player radius
      const expandedBox = wallBox.clone();
      expandedBox.expandByScalar(playerRadius);
      
      // Check if the proposed position is inside the expanded wall box
      if (expandedBox.containsPoint(proposedPosition)) {
        // Check if this is the victory wall
        if (wall.isVictoryWall) {
          setHasWon(true);
          return false; // Allow passing through victory wall
        }
        return true; // Collision detected
      }
    }
    
    return false; // No collision
  };

  // Restart function for victory screen
  const handleRestart = () => {
    setHasWon(false);
    
    // Reset camera position
    if (cameraRef.current) {
      cameraRef.current.position.set(0, 1.6, 0);
      cameraRotation.current.x = 0;
      cameraRotation.current.y = 0;
    }
  };

  useEffect(() => {
    // Scene setup
    const scene = new THREE.Scene();
    sceneRef.current = scene;
    scene.background = new THREE.Color(0x87ceeb);
    
    // Add some light
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambientLight);
    const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
    directionalLight.position.set(0, 10, 5);
    scene.add(directionalLight);

    // Camera setup
    const camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    camera.position.y = 1.6; // Eye height
    cameraRef.current = camera;

    // Renderer setup
    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    if (mountRef.current) {
      mountRef.current.appendChild(renderer.domElement);
    }
    rendererRef.current = renderer;

    // Create a floor
    const floorGeometry = new THREE.PlaneGeometry(50, 50, 10, 10);
    const floorMaterial = new THREE.MeshStandardMaterial({
      color: 0x808080,
      side: THREE.DoubleSide,
    });
    const floor = new THREE.Mesh(floorGeometry, floorMaterial);
    floor.rotation.x = -Math.PI / 2;
    scene.add(floor);

    // Clear walls array before adding new ones
    wallsRef.current = [];

    // Helper function to create walls with optional texture
    const createWall = (scene, x, y, z, width, depth, height, textureURL, isVictory = false) => {
      const wallGeometry = new THREE.BoxGeometry(width, height, depth);
      
      let wallMaterial;
      
      if (textureURL) {
        const texture = loadTexture(textureURL);
        
        wallMaterial = new THREE.MeshStandardMaterial({
          map: texture,
          roughness: 0.7,
          metalness: 0.2
        });
      } else {
        // Create a plain colored material if no texture URL is provided
        wallMaterial = new THREE.MeshStandardMaterial({
          color: isVictory ? 0x00FF00 : 0x8b4F39,
          roughness: 0.7,
          metalness: 0.5
        });
      }
      
      const wall = new THREE.Mesh(wallGeometry, wallMaterial);
      wall.position.set(x, y, z);
      scene.add(wall);
      
      // Store wall data for minimap and collision
      wallsRef.current.push({
        x: x,
        z: z,
        width: width,
        depth: depth,
        rotationY: wall.rotation.y,
        mesh: wall,
        isVictoryWall: isVictory  // Flag for victory wall
      });
      
      return wall;
    };

    // Create walls
    createWall(
      scene, 
      0.5, 2.5, 2,              // Position (x, y, z)
      5, 0.5, 5,              // Size (width, depth, height)
      '/Images/row4.png' 
    );

    createWall(
      scene, 
      3, 2.5, -1,              // Position (x, y, z)
      0.5, 6, 5,              // Size (width, depth, height)
      '/Images/tiger.jpg'    // Texture URL
    );

    
    createWall(
      scene, 
      -8, 2.5, 2,             // Position (x, y, z)
      8, 0.5, 5,             // Size (width, depth, height)
      '/Images/jerry.jpg'
    );
    
    createWall(
      scene, 
      -6, 2.5, -4,             // Position (x, y, z)
      18, 0.5, 5,              // Size (width, depth, height)
      '/Images/row1.png'
    );
    
    createWall(
      scene, 
      -15, 2.5, 2,             // Position (x, y, z)
      0.5, 12, 5,               // Size (width, depth, height)
      '/Images/row5.png'
    );

    createWall(
      scene, 
      -1.5, 2.5, 4,             // Position (x, y, z)
      0.5, 4, 5,               // Size (width, depth, height)
      '/Images/future.webp'
    );

    createWall(
      scene, 
      -4.5, 2.5, 5,             // Position (x, y, z)
      0.5, 6, 5,               // Size (width, depth, height)
      '/Images/sunny.jpg'
    );

    createWall(
      scene, 
      -8, 2.5, 8,             // Position (x, y, z)
      14, 0.5, 5,               // Size (width, depth, height)
      '/Images/row2.png'
    );

    createWall(
      scene, 
      6, 2.5, 2,             // Position (x, y, z)
      0.5, 34, 5,               // Size (width, depth, height)
      '/Images/scenary1.jpg'
    );

    createWall(
      scene, 
      3, 2.5, 10,             // Position (x, y, z)
      0.5, 10, 5,               // Size (width, depth, height)
      '/Images/row3.png'
    );

    createWall(
      scene, 
      -4, 2.5, 15,             // Position (x, y, z)
      14, 0.5, 5,               // Size (width, depth, height)
      '/Images/peepshow.jpg'
    );

    createWall(
      scene, 
      -10, 2.5, 11.5,             // Position (x, y, z)
      0.5, 7, 5,                // Size (width, depth, height)
      '/Images/jerma.webp'
    );

    createWall(
      scene, 
      -5, 2.5, -10,             // Position (x, y, z)
      14, 0.5, 5,                // Size (width, depth, height)
      '/Images/row6.png'
    );

    createWall(
      scene, 
      2, 2.5, -15,             // Position (x, y, z)
      8, 0.5, 5,               // Size (width, depth, height)
      '/Images/pikachu.jpg'
    );
    createWall(
      scene, 
      -2, 2.5, -12.5,             // Position (x, y, z)
      0.5, 5, 5,               // Size (width, depth, height)
    );

    createWall(
      scene, 
      -4, 2.5, 16.5,             // Position (x, y, z)
      0.5, 3, 5,               // Size (width, depth, height)
    );

    createWall(
      scene, 
      1, 2.5, 18,             // Position (x, y, z)
      10, 0.5, 5,               // Size (width, depth, height)
      '/Images/sonic.jpg'
    );

    createWall(
      scene, 
      -15, 2.5, -9,             // Position (x, y, z)
      0.5, 10, 5,               // Size (width, depth, height)
      '/Images/messi.jpg'
    );

    createWall(
      scene, 
      -13, 2.5, -15,             // Position (x, y, z)
      1, 1, 2,               // Size (width, depth, height)
      '/Images/trophy.png',
      true                   // Is victory wall
    );

    // Create a simple crosshair
    const crosshair = document.createElement('div');
    crosshair.style.position = 'absolute';
    crosshair.style.top = '50%';
    crosshair.style.left = '50%';
    crosshair.style.width = '10px';
    crosshair.style.height = '10px';
    crosshair.style.backgroundColor = 'transparent';
    crosshair.style.border = '2px solid white';
    crosshair.style.borderRadius = '50%';
    crosshair.style.transform = 'translate(-50%, -50%)';
    crosshair.style.zIndex = '1000';
    document.body.appendChild(crosshair);
    crosshairRef.current = crosshair;
    /*
    // Create minimap canvas
    const minimap = document.createElement('canvas');
    minimap.width = 200;
    minimap.height = 200;
    minimap.style.position = 'absolute';
    minimap.style.bottom = '20px';
    minimap.style.right = '20px';
    minimap.style.border = '2px solid white';
    minimap.style.backgroundColor = 'rgba(0, 0, 0, 0.5)';
    minimap.style.zIndex = '1000';
    document.body.appendChild(minimap);
    minimapRef.current = minimap;
    minimapCtxRef.current = minimap.getContext('2d');
    */
    // Handle window resize
    const handleResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(window.innerWidth, window.innerHeight);
    };
    window.addEventListener('resize', handleResize);

    // Keyboard controls
    const onKeyDown = (event) => {
      keysPressed.current[event.code] = true;
    };

    const onKeyUp = (event) => {
      keysPressed.current[event.code] = false;
    };

    document.addEventListener('keydown', onKeyDown);
    document.addEventListener('keyup', onKeyUp);

    // Pointer lock for mouse look
    const lockPointer = () => {
      if (mountRef.current) {
        mountRef.current.requestPointerLock = 
          mountRef.current.requestPointerLock ||
          mountRef.current.mozRequestPointerLock ||
          mountRef.current.webkitRequestPointerLock;
        
        mountRef.current.requestPointerLock();
      }
    };

    if (mountRef.current) {
      mountRef.current.addEventListener('click', lockPointer);
    }

    const pointerLockChange = () => {
      if (document.pointerLockElement === mountRef.current ||
          document.mozPointerLockElement === mountRef.current ||
          document.webkitPointerLockElement === mountRef.current) {
        pointerLockEnabled.current = true;
      } else {
        pointerLockEnabled.current = false;
      }
    };

    document.addEventListener('pointerlockchange', pointerLockChange);
    document.addEventListener('mozpointerlockchange', pointerLockChange);
    document.addEventListener('webkitpointerlockchange', pointerLockChange);

    // Mouse movement for looking around
    const onMouseMove = (event) => {
      if (!pointerLockEnabled.current) return;
      
      const movementX = event.movementX || event.mozMovementX || event.webkitMovementX || 0;
      const movementY = event.movementY || event.mozMovementY || event.webkitMovementY || 0;
      
      // Update the camera rotation values
      cameraRotation.current.y -= movementX * 0.002; // Yaw (looking left/right)
      cameraRotation.current.x -= movementY * 0.002; // Pitch (looking up/down)
      
      // Clamp the vertical rotation to prevent over-rotation
      cameraRotation.current.x = Math.max(
        -Math.PI / 2 + 0.01, 
        Math.min(Math.PI / 2 - 0.01, cameraRotation.current.x)
      );
      
      // Apply rotation using quaternion to avoid gimbal lock
      camera.quaternion.setFromEuler(
        new THREE.Euler(
          cameraRotation.current.x,
          cameraRotation.current.y,
          0,
          'YXZ' // Order matters! YXZ for proper FPS control
        )
      );
    };

    document.addEventListener('mousemove', onMouseMove);

    // Function to update minimap
    const updateMinimap = () => {
      if (!minimapCtxRef.current) return;
      
      const ctx = minimapCtxRef.current;
      const mapSize = 200;
      const mapScale = 5; // Scale factor: 1 unit in 3D = 5 pixels on minimap
      const centerX = mapSize / 2;
      const centerY = mapSize / 2;
      
      // Clear minimap
      ctx.clearRect(0, 0, mapSize, mapSize);
      
      // Draw background
      ctx.fillStyle = 'rgba(20, 20, 40, 0.8)';
      ctx.fillRect(0, 0, mapSize, mapSize);
      
      // Draw grid
      ctx.strokeStyle = 'rgba(100, 100, 100, 0.3)';
      ctx.lineWidth = 1;
      
      // Vertical grid lines
      for (let x = -25; x <= 25; x += 5) {
        ctx.beginPath();
        ctx.moveTo(centerX + x * mapScale, 0);
        ctx.lineTo(centerX + x * mapScale, mapSize);
        ctx.stroke();
      }
      
      // Horizontal grid lines
      for (let z = -25; z <= 25; z += 5) {
        ctx.beginPath();
        ctx.moveTo(0, centerY + z * mapScale);
        ctx.lineTo(mapSize, centerY + z * mapScale);
        ctx.stroke();
      }
      
      // Draw walls
      ctx.fillStyle = 'rgba(139, 69, 19, 0.8)';
      wallsRef.current.forEach(wall => {
        const { x, z, width, depth } = wall;
        
        // Draw rotated wall (account for wall.rotation if needed)
        ctx.save();
        ctx.translate(centerX + x * mapScale, centerY + z * mapScale);
        
        // Handle wall rotation if your walls can be rotated
        if (wall.rotationY) {
          ctx.rotate(-wall.rotationY);
        }
        
        // Draw the wall rectangle
        ctx.fillRect(
          -width * mapScale / 2, 
          -depth * mapScale / 2, 
          width * mapScale, 
          depth * mapScale
        );
        ctx.restore();
      });
      
      // Draw player position and direction
      const playerX = centerX + camera.position.x * mapScale;
      const playerZ = centerY + camera.position.z * mapScale;
      
      // Player dot
      ctx.fillStyle = 'rgb(0, 255, 0)';
      ctx.beginPath();
      ctx.arc(playerX, playerZ, 3, 0, Math.PI * 2);
      ctx.fill();
      
      // Player direction
      ctx.strokeStyle = 'rgb(0, 255, 0)';
      ctx.lineWidth = 2;
      ctx.beginPath();
      ctx.moveTo(playerX, playerZ);
      ctx.lineTo(
        playerX + Math.sin(cameraRotation.current.y) * -10,
        playerZ + Math.cos(cameraRotation.current.y) * -10
      );
      ctx.stroke();
      
      // Draw minimap label
      ctx.fillStyle = 'white';
      ctx.font = '10px Arial';
      ctx.textAlign = 'left';
      ctx.fillText('MINIMAP', 5, 12);
    };

    // Animation loop
    const animate = () => {
      requestAnimationFrame(animate);
      
      const time = performance.now();
      const delta = (time - prevTime.current) / 1000; // in seconds
      
      // Apply friction
      velocity.current.x -= velocity.current.x * 10.0 * delta;
      velocity.current.z -= velocity.current.z * 10.0 * delta;
      
      // Reset direction
      direction.current.z = 0;
      direction.current.x = 0;
      
      // Movement controls with WASD or arrow keys
      if (keysPressed.current['KeyW'] || keysPressed.current['ArrowUp']) {
        direction.current.z = -1;
      }
      if (keysPressed.current['KeyS'] || keysPressed.current['ArrowDown']) {
        direction.current.z = 1;
      }
      if (keysPressed.current['KeyA'] || keysPressed.current['ArrowLeft']) {
        direction.current.x = -1;
      }
      if (keysPressed.current['KeyD'] || keysPressed.current['ArrowRight']) {
        direction.current.x = 1;
      }
      
      // Normalize direction vector
      if (direction.current.x !== 0 || direction.current.z !== 0) {
        direction.current.normalize();
      }
      
      // Move in the direction we're facing
      const moveSpeed = 50.0; // units per second
      
      // Create a direction vector based on camera rotation (yaw only)
      const moveDirection = new THREE.Vector3();
      
      // Forward/backward movement
      if (direction.current.z !== 0) {
        // Use camera's forward direction (ignoring up/down pitch)
        moveDirection.set(
          Math.sin(cameraRotation.current.y) * direction.current.z,
          0,
          Math.cos(cameraRotation.current.y) * direction.current.z
        );
      }
      
      // Left/right movement (perpendicular to forward)
      if (direction.current.x !== 0) {
        moveDirection.add(
          new THREE.Vector3(
            Math.sin(cameraRotation.current.y + Math.PI/2) * direction.current.x,
            0,
            Math.cos(cameraRotation.current.y + Math.PI/2) * direction.current.x
          )
        );
      }
      
      // Normalize and apply movement
      if (moveDirection.length() > 0) {
        moveDirection.normalize();
        
        // Proposed new position
        const proposedPosition = camera.position.clone().add(
          new THREE.Vector3(
            moveDirection.x * moveSpeed * delta,
            0,
            moveDirection.z * moveSpeed * delta
          )
        );
        
        // Check for wall collision
        const isColliding = checkWallCollision(
          camera, 
          wallsRef.current, 
          proposedPosition
        );
        
        // Apply movement only if not colliding
        if (!isColliding) {
          velocity.current.x += moveDirection.x * moveSpeed * delta;
          velocity.current.z += moveDirection.z * moveSpeed * delta;
        }
      }
      
      // Apply velocity to position
      const newPosX = camera.position.x + velocity.current.x * delta;
      const newPosZ = camera.position.z + velocity.current.z * delta;
      
      // Check collision for final position
      const finalProposedPosition = new THREE.Vector3(newPosX, camera.position.y, newPosZ);
      const isColliding = checkWallCollision(
        camera, 
        wallsRef.current, 
        finalProposedPosition
      );
      
      // Apply position only if not colliding
      if (!isColliding) {
        camera.position.x = newPosX;
        camera.position.z = newPosZ;
      }
      
      // Jump (spacebar)
      if (keysPressed.current['Space'] && camera.position.y <= 1.6) {
        velocity.current.y = 5;
      }
      
      // Apply gravity
      velocity.current.y -= 9.8 * delta;
      camera.position.y += velocity.current.y * delta;
      
      // Prevent falling below ground
      if (camera.position.y < 1.6) {
        velocity.current.y = 0;
        camera.position.y = 1.6;
      }
      
      // Update minimap
      //updateMinimap();
      
      // Render the scene
      renderer.render(scene, camera);
      prevTime.current = time;
    };
    
    const animationId = requestAnimationFrame(animate);
    
    // Cleanup function
    return () => {
      // Safe removal of event listeners
      window.removeEventListener('resize', handleResize);
      document.removeEventListener('keydown', onKeyDown);
      document.removeEventListener('keyup', onKeyUp);
      document.removeEventListener('mousemove', onMouseMove);
      document.removeEventListener('pointerlockchange', pointerLockChange);
      document.removeEventListener('mozpointerlockchange', pointerLockChange);
      document.removeEventListener('webkitpointerlockchange', pointerLockChange);
      
      // Safe removal of DOM elements
      if (mountRef.current) {
        mountRef.current.removeEventListener('click', lockPointer);
        if (rendererRef.current) {
          rendererRef.current.dispose();
          if (mountRef.current.contains(rendererRef.current.domElement)) {
            mountRef.current.removeChild(rendererRef.current.domElement);
          }
        }
      }
      
      // Safe removal of crosshair
      if (crosshairRef.current && document.body.contains(crosshairRef.current)) {
        document.body.removeChild(crosshairRef.current);
      }
      
      // Safe removal of minimap
      if (minimapRef.current && document.body.contains(minimapRef.current)) {
        document.body.removeChild(minimapRef.current);
      }
      
      // Cancel animation frame
      cancelAnimationFrame(animationId);
    };
  }, []);

if (hasWon) {
    return <CelebrationScreen onRestart={handleRestart} />;
  }

  return (
    <div 
      ref={mountRef} 
      style={{ 
        width: '100%', 
        height: '100vh', 
        overflow: 'hidden'
      }}
    >
      <div style={{
        position: 'absolute',
        bottom: '20px',
        left: '20px',
        color: 'white',
        background: 'rgba(0,0,0,0.5)',
        padding: '10px',
        borderRadius: '5px',
        zIndex: '100'
      }}>
        <h3>Controls:</h3>
        <p>WASD/Arrow Keys: Move</p>
        <p>Mouse: Look around</p>
        <p>Space: Jump</p>
        <p>Click to lock mouse pointer</p>
        <p>Reach the green wall to win!</p>
      </div>
    </div>
  );
};

export default FPSControls;